import React from './react'


function trick() {
    const element = (
        <div>
            <h1 className="title" data-item="1">Hello World!</h1>
            <h6 style="color:red;">{new Date()}</h6>
        </div>
    )
    React.render(element, document.getElementById('root'))
}

setInterval(trick, 1000);

